:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
html {
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
  min-height: 100vh;
}
body {
  color: var(--foreground);
  width: 100%;
  height: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
@media (max-width: 640px) {
  .heading-mobile-shadow {
    text-shadow: 0 2px 4px rgba(222, 222, 222, 0.25);
  }
}
@media screen and (max-width: 640px) {
  .cloudy-text {
    position: relative;
    z-index: 1;
  }
  .cloudy-text::before {
    content: "";
    position: absolute;
    inset: -20px;
    background: radial-gradient(
      circle at center,
      rgba(222, 222, 222, 0.65) 0%,
      rgba(222, 222, 222, 0.25) 45%,
      rgba(222, 222, 222, 0.1) 70%,
      rgba(222, 222, 222, 0) 100%
    );
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    z-index: -1;
    filter: blur(10px);
  }
}
.difference-card {
  box-shadow: 0px 3px 8px 0px #96b3bf4d;
