  border: 1px solid #2320611a;
  padding: 32px 20px;
}
@media screen and (max-width: 640px) {
  .difference-card {
    padding: 32px 24px;
  }
}
.difference-card:hover {
  box-shadow: 0px 4px 16px 0px #96b3bf99;
}
.font-roboto {
  font-synthesis: none;
